      .bloque-texto-g__texto.p-4
        p.mb-0 Un Sistema de Informacin (SI) es un conjunto de componentes interrelacionados que trabajan juntos para recopilar, procesar, almacenar y difundir la informacin necesaria para la toma de decisiones asertivas dentro de la organizacin.
    .row.align-items-center.mb-3
      .col-lg-4.mb-3.mb-lg-0(data-aos="fade-right")
        figure
          img(src='@/assets/curso/tema1/img2.png', style="width: 370px", alt='Imgen decorativa').m-auto
      .col-lg-8(data-aos="fade-left")
        p El propsito de un SI es convertir los recursos en bruto en informacin til que pueda usarse de manera estratgica en los procesos empresariales.
        .row.align-items-center.mb-3
          .col-auto
            figure
              img(src='@/assets/curso/tema1/img1.svg', style="width: 30px", alt='Imgen decorativa').m-auto
          .col
            p.mb-0 Por su parte, la tecnologa de TI es aquella que impulsa o permite el almacenamiento, el procesamiento y el flujo de comunicacin dentro de una organizacin. Todo lo relacionado con las computadoras, #[i #[i software]], redes, intranets, sitios web, servidores, bases de datos y telecomunicaciones.
        .cajon.color-acento-botones.p-4.mb-3
          p.mb-0 La mayora de las empresas actualmente dependen de sistemas informticos para la gestin de sus operaciones, por ejemplo, el correo electrnico, las bases de datos, la administracin de sitios web, entre otros, son fundamentales para el ptimo funcionamiento de los procesos organizacionales.
        p.mb-0 Por ejemplo, los hospitales cuentan con grandes bases de datos de pacientes para poder realizar un seguimiento eficiente de las historias clnicas. Las universidades cuentan con sistemas para la gestin de personal, estudiantes y pagos, as como redes en expansin para la administracin del campus. Incluso una pequea empresa de entrega de alimentos a domicilio necesita un sistema de gestin y seguimiento de pedidos.
    p.mb-5(data-aos="fade-right") Si bien los sistemas de informacin pueden diferir en la forma en que se utilizan dentro de una organizacin, todos tienen los siguientes componentes:
    .row.justify-content-center.mb-5(data-aos="fade-down")
      .col-lg-10
        ImagenInfografica.color-secundario
          template(v-slot:imagen)
            figure
              img(src='@/assets/curso/tema1/img2.svg', alt='Imgen decorativa')
          .tarjeta.color-secundario.p-3(x="22.3%" y="36%" numero="+")
            p #[b #[i Hardware]]
            p.mb-0 Conjunto de elementos fsicos o materiales que constituyen una computadora o un sistema informtico.
          .tarjeta.color-secundario.p-3(x="76%" y="64%" numero="+")
            p #[b #[i #[i Software]]]
            p.mb-0 Son los programas utilizados para la administracin, procesamiento y anlisis de la informacin.
          .tarjeta.color-secundario.p-3(x="65%" y="88%" numero="+")
            p #[b Red]
            p.mb-0 Posibilita que muchos recursos deben estar conectados entre s, especialmente si muchas personas diferentes en una organizacin usan el mismo sistema.
          .tarjeta.color-secundario.p-3(x="10%" y="80%" numero="+")
            p #[b Procedimiento]
            p.mb-0 Describe cmo se procesan y analizan datos y los recursos especficos lo que permite determinar respuestas a diferentes problemticas a partir de las cuales fue diseado el sistema (lgica de negocio)
          .tarjeta.color-secundario.p-3(x="50.6%" y="55.6%" numero="+")
            p #[b Base de datos]
            p.mb-0 Todos los sistemas funcionan con recursos organizados en tablas y archivos.
    .row.mb-4(data-aos="fade-right")
      .col-auto.px-4.py-3.bg-c3
        h3.mb-0 Tipos de sistemas de Informacin en las organizaciones
    p.mb-5(data-aos="fade-right") Existen diferentes tipos de sistemas de informacin con funciones diferentes los cuales pueden ser utilizados segn requerimientos organizacionales, a continuacin, se presentan algunos:
    .tarjeta.tarjeta--azul.p-4(data-aos="fade-left")
      SlyderA(tipo="b")
        .row.justify-content-center.m-1
          .col-11.bg-c4.p-4
            .row.align-items-center.justify-content-center
              .col-lg-4.mb-4.mb-lg-0
                figure
                  img(src='@/assets/curso/tema1/img3.png', style="width: 300px", alt='Imgen decorativa').m-auto
              .col-lg-6
                p #[b.txt-c1 Sistema de gestin de bases de datos (DBMS)]
                p.mb-0 DataBase Management System, es una combinacin de #[i #[i software]] que permite la administracin y el anlisis de datos. Su funcin principal es servir de interfaz entre la base de datos, el usuario y las distintas aplicaciones utilizadas.
        .row.justify-content-center.m-1
          .col-11.bg-c4.p-4
            .row.align-items-center.justify-content-center
              .col-lg-4.mb-4.mb-lg-0
                figure
                  img(src='@/assets/curso/tema1/img4.png', style="width: 300px", alt='Imgen decorativa').m-auto
              .col-lg-6
                p #[b.txt-c1 Hoja de clculo]
                p.mb-0 Es una herramienta para el anlisis bsico; se basa en frmulas que definen relaciones entre recursos. Se puede usar para calcular promedios para un conjunto de valores o para ver cmo cambia un valor con el tiempo.
        .row.justify-content-center.m-1
          .col-11.bg-c4.p-4
            .row.align-items-center.justify-content-center
              .col-lg-4.mb-4.mb-lg-0
                figure
                  img(src='@/assets/curso/tema1/img5.png', style="width: 300px", alt='Imgen decorativa').m-auto
              .col-lg-6
                p #[b.txt-c1 Enterprise Resource Planning (ERP)]
                p.mb-0 Sistema especializado que ha sido diseado especficamente para dar soporte a un determinado proceso dentro de una organizacin o para realizar tareas de anlisis muy concreta, este sistema se utiliza para integrar la gestin de todo el proceso interno y la cadena de suministro externa a lo largo de una organizacin.
        .row.justify-content-center.m-1
          .col-11.bg-c4.p-4
            .row.align-items-center.justify-content-center
              .col-lg-4.mb-4.mb-lg-0
                figure
                  img(src='@/assets/curso/tema1/img6.png', style="width: 300px", alt='Imgen decorativa').m-auto
              .col-lg-6
                p #[b.txt-c1 Sistema de Informacin Geogrfica (SIG)]
                p.mb-0 Tambin  citado como GIS por sus siglas en ingls <i>Geographical Information System</i>, es un conjunto de herramientas que integra y relaciona diversos componentes que permiten la organizacin, almacenamiento, manipulacin, anlisis y modelizacin de grandes cantidades de datos procedentes del entorno que estn vinculados a una referencia espacial, facilitando la incorporacin de aspectos sociales-culturales, econmicos y ambientales que permiten la  toma de decisiones de una manera ms eficaz.
        .row.justify-content-center.m-1
          .col-11.bg-c4.p-4
            .row.align-items-center.justify-content-center
              .col-lg-4.mb-4.mb-lg-0
                figure
                  img(src='@/assets/curso/tema1/img7.png', style="width: 300px", alt='Imgen decorativa').m-auto
              .col-lg-6
                p #[b.txt-c1 Sistemas de soporte de decisiones (DSS)]
                p.mb-0 Son aplicaciones de #[i software] utilizadas por la gerencia media y alta para recopilar informacin de una amplia gama de fuentes y procesos para respaldar la resolucin de problemas y la toma de decisiones. Un DSS se utiliza principalmente para problemas de decisin no estructurados y semiestructurados.
        .row.justify-content-center.m-1
          .col-11.bg-c4.p-4
            .row.align-items-center.justify-content-center
              .col-lg-4.mb-4.mb-lg-0
                figure
                  img(src='@/assets/curso/tema1/img8.png', style="width: 300px", alt='Imgen decorativa').m-auto
              .col-lg-6
                p #[b.txt-c1 Sistemas de Informacin Ejecutiva (EIS)]
                p.mb-0 Son herramientas de informes que brindan acceso rpido a informes y procesos resumidos, provenientes de todos los niveles y departamentos de la empresa, como contabilidad, recursos humanos y operaciones.
