            |
            p.mb-0 El #[i software] de productividad es un subconjunto del #[i software] de aplicacin que proporciona funcionalidad comercial bsica a una computadora personal: procesamiento de textos, hojas de clculo y presentaciones.
          li.d-flex
            i.fas.fa-check-circle(style="color: #4ADBD1")
            |
            p.mb-0 Un sistema ERP es una aplicacin de #[i software] con una base de datos centralizada que se implementa en toda la organizacin.
          li.d-flex
            i.fas.fa-check-circle(style="color: #4ADBD1")
            |
            p.mb-0 La computacin en la nube es un mtodo de entrega de #[i software] que se ejecuta en cualquier computadora que tenga un navegador web y acceso a Internet.
          li.d-flex
            i.fas.fa-check-circle(style="color: #4ADBD1")
            |
            p.mb-0 El #[i software] se desarrolla a travs de un proceso llamado programacin, en el que un programador usa un lenguaje especfico para ejecutarlo. Si bien la mayora del #[i software] se desarrolla utilizando un modelo de cdigo cerrado, el movimiento de cdigo abierto est ganando ms apoyo en la actualidad.
      .col-lg-5(data-aos="fade-left")
        figure
          img(src='@/assets/curso/tema1/img4.svg', style="width: 470px", alt='Imgen decorativa').m-auto
    .row.justify-content-center.mb-5(data-aos="fade-left")
      .col-lg-10
        .row.justify-content-center.align-items-center
          .col-xl-4 
            figure
              img(src='@/assets/curso/tema1/img5.svg', style="width: 380px", alt='Imgen decorativa').m-auto
          .col-xl-auto
            figure.fig-p2
              img(src='@/assets/curso/tema1/img6.svg', style="width: 50px", alt='Imgen decorativa').m-auto
          .col-md-8.col-xl-7.bg-c5.p-4
            p.text-white.mb-0 #[b Recuerde que el #[i software]] es el conjunto de instrucciones que le dicen al #[i hardware] qu hacer. El #[i software] se puede dividir ampliamente en dos categoras: sistemas operativos y #[i software] de aplicacin. Los sistemas operativos administran el #[i hardware] y crean la interfaz entre el #[i hardware] y el usuario. El #[i software] de aplicacin es la categora de programas que hacen algo til para el usuario.
    .row.mb-4(data-aos="fade-right")
      .col-auto.px-4.py-3.bg-c3
        h3.mb-0 Sistemas operativos
    p.mb-5(data-aos="fade-right") El sistema operativo proporciona varias funciones esenciales, que incluyen:
    .row.bg-c1.mb-5
      .col-12
        .px-5
          .row.justify-content-center.mb-5(data-aos="fade-right")
            .col-md-8.col-lg.bg-c6.shadow.m-2
              .p-4
                figure.mb-4
                  img(src='@/assets/curso/tema1/img10.svg', style="width: 140px", alt='Imgen decorativa').m-auto
                p.text-center.text-white.mb-0 #[b Administrar los recursos de #[i hardware] de la computadora.]
            .col-md-8.col-lg.bg-c7.shadow.m-2
              .p-4
                figure.mb-4
                  img(src='@/assets/curso/tema1/img11.svg', style="width: 140px", alt='Imgen decorativa').m-auto
                p.text-center.mb-0 #[b Proporcionar los componentes de la interfaz de usuario.]
            .col-md-8.col-lg.bg-c8.shadow.m-2
              .p-4
                figure.mb-4
                  img(src='@/assets/curso/tema1/img12.svg', style="width: 140px", alt='Imgen decorativa').m-auto
                p.text-center.text-white.mb-0 #[b Proporcionar una plataforma para que los desarrolladores de #[i software] escriban aplicaciones.]
          p.text-center.mb-5(data-aos="fade-down") #[b Para saber ms sobre sistemas operativos revise con atencin el siguiente recurso:]
          figure(data-aos="fade-left")
            .video
              iframe(width="560" height="315" src="https://www.youtube.com/embed/eNG_O4A2Yhs" title="Sistemas operativos" frameborder="0" allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture" allowfullscreen)
    .row.mb-4(data-aos="fade-right")
      .col-auto.px-4.py-3.bg-c3
        h3.mb-0 Diferencia entre los sistemas operativos Mac vs. Windows
    .bloque-texto-a.color-secundario.p-4.p-md-5.mb-5(data-aos="fade-left")
      .row.m-0.align-items-center.justify-content-between
        .col-lg-4.mb-4.mb-lg-0
          figure
            img(src='@/assets/curso/tema1/img13.svg', style="width: 280px", alt='Imgen decorativa').m-auto
        .col-lg-8
          .bloque-texto-a__texto.p-4
            p Eres un Mac? Eres un PC? Desde su introduccin en 1984, los usuarios de Apple Macintosh han sido bastante sesgados acerca de su preferencia por el sistema operativo Macintosh (ahora llamado OS X) sobre el de Microsoft. Cuando Microsoft introdujo Windows, Apple demand a Microsoft, alegando que copian la apariencia del sistema operativo Macintosh. Al final, Microsoft se defendi con xito.
    .row.justify-content-center.align-items-center.mb-5
      .col-lg-auto.mb-4.mb-lg-0(data-aos="fade-right")
        figure
          img(src='@/assets/curso/tema1/img14.svg', style="width: 750px", alt='En los ltimos aos, Microsoft y Apple han intercambiado crticas entre s, cada uno afirmando tener un mejor sistema operativo y software. Si bien Microsoft siempre ha tenido la mayor participacin de mercado, Apple ha sido el favorito de los artistas, msicos y la lite tecnolgica. Apple tambin proporciona muchas computadoras a las escuelas primarias, ganando as seguidores entre la generacin ms joven.').m-auto
      .col-lg-3(data-aos="fade-left")
        figure
          img(src='@/assets/curso/tema1/img15.svg', style="width: 450px", alt='Imgen decorativa').m-auto
    .row.mb-4(data-aos="fade-right")
      .col-auto.px-4.py-3.bg-c3
        h3.mb-0 Por qu el #[i software] de Microsoft es tan dominante en el mundo de los negocios?
    figure(data-aos="fade-right")
      img(src='@/assets/curso/tema1/img9.png', style="width: 1200px", alt='Imgen decorativa').m-auto
    .row.justify-content-center.mb-5(data-aos="fade-down")
      .col-lg-10.bg-c2.shadow-sm.p-4
        p.mb-0 Si han trabajado en el mundo de los negocios, se habrn dado cuenta de que casi todas las computadoras ejecutan una versin del sistema operativo Windows de Microsoft. Por qu es esto? En casi todos los campus universitarios, se ve una preponderancia de computadoras porttiles Apple Macintosh. En las escuelas primarias, Apple tambin reina. Por qu esto no se ha extendido al mundo de los negocios?
    .row.align-items-center.mb-3
      .col-lg-4.mb-3.mb-lg-0(data-aos="fade-right")
        figure
          img(src='@/assets/curso/tema1/img10.png', style="width: 370px", alt='Imgen decorativa').m-auto
      .col-lg-8(data-aos="fade-left")
        p Casi todas las empresas usaban computadoras centrales IBM en las dcadas de 1960 y 1970. Estas mismas empresas se alejaron de las computadoras personales hasta que IBM lanz la PC en 1981. Cuando los ejecutivos tenan que tomar una decisin sobre la compra de computadoras personales para sus empleados, elegan la ruta segura y compraban IBM. El dicho entonces era: Nunca despidieron a nadie por comprar IBM. Entonces, durante la siguiente dcada, las empresas compraron computadoras personales IBM (o aquellas compatibles con ellas), que ejecutaban un sistema operativo llamado DOS. DOS fueron creados por Microsoft, por lo que cuando Microsoft lanz Windows como la prxima iteracin de DOS, las empresas tomaron la ruta segura y comenzaron a comprar Windows.
        .cajon.color-acento-botones.p-4
          p.mb-0 Microsoft pronto se encontr con el sistema operativo de computadora personal dominante para las empresas. 
    .row.align-items-center.mb-5(data-aos="fade-down")
      .col-auto
        figure
          img(src='@/assets/curso/tema1/img1.svg', style="width: 30px", alt='Imgen decorativa').m-auto
      .col
        p.mb-0 A medida que la computadora personal en red comenz a reemplazar a la computadora central como la forma principal de computacin dentro de las empresas, se volvi esencial para Microsoft brindarles a las empresas la capacidad de administrar y proteger sus redes. Microsoft desarroll productos de servidor de nivel comercial para acompaar sus productos de computadora personal, proporcionando as una solucin comercial completa. Y ahora, dice el dicho: Nadie fue despedido por comprar Microsoft.
    .row.mb-4(data-aos="fade-right")
      .col-auto.px-4.py-3.bg-c3
        h3.mb-0 #[i Software] de la aplicacin
    .row.justify-content-end.bg-c9.mb-5
      .col-lg-6.bg-c10.shadow-sm.p-5.my-4(data-aos="fade-right")
        p.mb-0 Este #[i software] permite al usuario lograr algn objetivo o propsito. Por ejemplo, si tiene que escribir un artculo, puede usar el programa de #[i software] de aplicacin Microsoft Word. Si quieres escuchar msica, puedes usar iTunes. Para navegar por la web, puedes usar Internet Explorer o Firefox. Incluso un juego de computadora podra considerarse #[i software] de aplicacin.
    .row.justify-content-center.mb-5(data-aos="fade-left")
      .col-lg-10
        .row.align-items-center
          .col-xl-7.bg-c5.p-4
            p.text-white.mb-0 Saba que una aplicacin asesina es aquella que se vuelve tan esencial que un gran nmero de personas comprar un dispositivo solo para ejecutar esa aplicacin. Para la computadora personal, la aplicacin asesina fue la hoja de clculo. En 1979, se present VisiCalc, el primer paquete de hoja de clculo para computadora personal. Fue un xito e impuls las ventas del Apple II. Tambin solidific el valor de la computadora personal ms all del crculo relativamente pequeo de fanticos de la tecnologa. Cuando se lanz la PC de IBM, otro programa de hoja de clculo, Lotus 1-2-3, fue la aplicacin definitiva para los usuarios comerciales.
          .col-xl-auto
            figure.fig-p1
              img(src='@/assets/curso/tema1/img17.svg', style="width: 50px", alt='Imgen decorativa').m-auto
          .col-xl-4 
            figure
              img(src='@/assets/curso/tema1/img16.svg', style="width: 380px", alt='Imgen decorativa').m-auto
    .row.mb-4(data-aos="fade-right")
      .col-auto.px-4.py-3.bg-c3
        h3.mb-0 #[i Software] de productividad
    p.mb-5(data-aos="fade-right") Junto con la hoja de clculo, varias otras aplicaciones de #[i software] se han convertido en herramientas estndar para el lugar de trabajo. Estas aplicaciones, denominadas #[i software] de productividad, permiten a los empleados de oficina completar su trabajo diario. Muchas veces, estas aplicaciones vienen empaquetadas juntas, como en el paquete Office de Microsoft. Aqu hay una lista de estas aplicaciones y sus funciones bsicas:
    TabsB.color-acento-contenido.mb-5(data-aos="fade-left")
      .py-4.py-md-5(titulo="Procesamiento de textos" :icono="require('@/assets/curso/tema1/img18.svg')")
        .row.align-items-center
          .col-lg-4.mb-4.mb-lg-0
            figure
              img(src='@/assets/curso/tema1/img12.png', style="width: 180px", alt='Imgen decorativa').m-auto
          .col-lg-8
            p.mb-0 Esta clase de #[i software] permite la creacin de documentos escritos. Las funciones incluyen la capacidad de escribir y editar texto, formatear fuentes y prrafos, y agregar, mover y eliminar texto en todo el documento. La mayora de los programas de procesamiento de texto modernos tambin tienen la capacidad de agregar tablas, imgenes y varias funciones de diseo y formato al documento. Los procesadores de texto guardan sus documentos como archivos electrnicos en una variedad de formatos. Con diferencia, el paquete de procesamiento de textos ms popular es Microsoft Word, que guarda sus archivos en formato DOCX. Este formato puede ser ledo/escrito por muchos otros paquetes de procesadores de texto.
      .py-4.py-md-5(titulo="Hoja de clculo" :icono="require('@/assets/curso/tema1/img19.svg')")
        .row.align-items-center
          .col-lg-4.mb-4.mb-lg-0
            figure
              img(src='@/assets/curso/tema1/img23.svg', style="width: 220px", alt='Imgen decorativa').m-auto
          .col-lg-8
            p.mb-0 Esta clase de #[i software] proporciona una forma de realizar clculos y anlisis numricos. El rea de trabajo se divide en filas y columnas, donde los usuarios pueden ingresar nmeros, texto o frmulas. Son las frmulas las que hacen que una hoja de clculo sea poderosa, lo que permite al usuario desarrollar clculos complejos que pueden cambiar segn los nmeros ingresados. La mayora de las hojas de clculo tambin incluyen la capacidad de crear grficos basados en los datos ingresados. El paquete de hojas de clculo ms popular es Microsoft Excel, que guarda sus archivos en formato XLSX. Al igual que con los procesadores de texto, muchos otros paquetes de hojas de clculo pueden leer y escribir en este formato de archivo.
      .py-4.py-md-5(titulo="Presentacin" :icono="require('@/assets/curso/tema1/img20.svg')")
        .row.align-items-center
          .col-lg-4.mb-4.mb-lg-0
            figure
              img(src='@/assets/curso/tema1/img24.svg', style="width: 270px", alt='Imgen decorativa').m-auto
          .col-lg-8
            p.mb-0 Esta clase de #[i software] proporciona la creacin de presentaciones de diapositivas. Remontndose a los das de los retroproyectores y las transparencias, el #[i software] de presentacin permite a sus usuarios crear un conjunto de diapositivas que se pueden imprimir o proyectar en una pantalla. Los usuarios pueden agregar texto, imgenes y otros elementos multimedia a las diapositivas. PowerPoint de Microsoft es el #[i software] ms popular en este momento, guardando sus archivos en formato PPTX.
      .py-4.py-md-5(titulo="Suites ofimticas" :icono="require('@/assets/curso/tema1/img21.svg')")
        .row.align-items-center
          .col-lg-4.mb-4.mb-lg-0
            figure
              img(src='@/assets/curso/tema1/img25.svg', style="width: 220px", alt='Imgen decorativa').m-auto
          .col-lg-8
            p.mb-0 Algunas suites ofimticas incluyen otros tipos de #[i software]. Por ejemplo, Microsoft Office incluye Outlook, su paquete de correo electrnico y OneNote, una herramienta de colaboracin para recopilar informacin. La versin profesional de Office tambin incluye Microsoft Access, un paquete de base de datos.
    p.mb-5(data-aos="fade-right") Microsoft populariz la idea del paquete de productividad de #[i software] de oficina con el lanzamiento de Microsoft Office.
    .row.align-items-center.mb-5
      .col-lg-6.mb-4.mb-lg-0(data-aos="fade-right")
        figure
          img(src='@/assets/curso/tema1/img26.svg', style="width: 500px", alt='Imgen decorativa').m-auto
      .col-lg-6(data-aos="fade-left")
        p Este paquete contina dominando el mercado y la mayora de las empresas esperan que los empleados sepan cmo usar este #[i software]. Sin embargo, existen muchos competidores de Microsoft Office y son compatibles con los formatos de archivo utilizados por Microsoft. Recientemente, Microsoft ha comenzado a ofrecer una versin web de su paquete de Office.
        .cajon.color-acento-botones.p-4
          p.mb-0 Similar a Google Drive, esta suite permite a los usuarios editar y compartir documentos en lnea utilizando tecnologa de computacin en la nube.
    .row.mb-4(data-aos="fade-right")
      .col-auto.px-4.py-3.bg-c3
        h3.mb-0 #[i Software] de utilidad y #[i software] de programacin
    .bloque-texto-g.bloque-texto-g--inverso.color-primario.p-3.p-sm-4.p-md-5.mb-5(data-aos="fade-left")
      .bloque-texto-g__img(
        :style="{'background-image': `url(${require('@/assets/curso/tema1/img13.png')})`}"
      )
      .bloque-texto-g__texto.p-4
        p.mb-0 Dos subcategoras de #[i software] de aplicacin que vale la pena mencionar son el #[i software] de utilidad y el #[i software] de programacin, revise a continuacin como se define cada uno de ellos.
    .row.justify-content-center.mb-5(data-aos="fade-down")
      .col-xl.col-lg-7.col-md-9.col-11.mb-4.mb-xl-0
        .crd_hover_txt
          .crd_hover_txt--img
            figure
              img(src="@/assets/curso/tema1/img14.png", alt="alt")
          .crd_hover_txt--body
            p.text-center #[b #[i Software] de Utilidad]
            p.text-center.mb-0 Permite reparar o modificar una computadora, por ejemplo, los #[i software] antivirus y #[i software] de desfragmentacin de disco. Permiten llenar las deficiencias de los sistemas operativos
      .col-xl.col-lg-7.col-md-9.col-11.mb-4.mb-xl-0
        .crd_hover_txt
          .crd_hover_txt--img
            figure
              img(src="@/assets/curso/tema1/img15.png", alt="alt")
          .crd_hover_txt--body1
            p.text-center.text-white #[b #[i Software] de programacin]
            p.text-center.text-white.mb-0 Su propsito es hacer ms #[i software]. La mayora de estos programas brindan a los programadores un entorno en el que pueden escribir el cdigo, probarlo y convertirlo al formato que luego se puede ejecutar en una computadora.
    .row.mb-4(data-aos="fade-right")
      .col-auto.px-4.py-3.bg-c3
        h3.mb-0 PowerPoint como herramienta
    p(data-aos="fade-right") Este #[i software] de presentacin ha ganado aceptacin como el mtodo principal para presentar informacin formalmente en un entorno empresarial, el arte de dar una presentacin atractiva se est volviendo raro. Muchos presentadores ahora solo leen las vietas en la presentacin e inmediatamente aburren a los asistentes, quienes ya pueden leerlo por s mismos.
    .row.align-items-center.mb-5
      .col-lg-7.mb-4.mb-lg-0(data-aos="fade-right")
        p El problema real no es tanto con PowerPoint como con la persona que crea y presenta, el #[i software] utilizado para ayudarlo a comunicarse no debe duplicar la presentacin que desea dar, sino que debe respaldarla.
        .cajon.color-acento-botones.p-4
          p.mb-0 Los desarrolladores de #[i software] tienen claro este problema, por ende, se estn desarrollando nuevas tecnologas de presentacin digital. Una nueva e innovadora aplicacin de presentacin es Prezi la cual es una herramienta de presentacin que permite a los presentadores colocar texto, imgenes y otros medios en un lienzo y luego navegar entre estos objetos a medida que presentan. Al igual que con PowerPoint, Prezi debe usarse para complementar la presentacin, se debe recordar que a veces las mejores presentaciones se hacen sin herramientas digitales.
      .col-lg-5(data-aos="fade-left")
        figure
          img(src='@/assets/curso/tema1/img16.png', style="width: 450px", alt='Imgen decorativa').m-auto
    .row.mb-4(data-aos="fade-right")
      .col-auto.px-4.py-3.bg-c3
        h3.mb-0 Barra lateral: Soy dueo de este #[i software], verdad? Bueno
    .bloque-texto-g.color-acento-contenido.p-3.p-sm-4.p-md-5.mb-5(data-aos="fade-right")
      .bloque-texto-g__img(
        :style="{'background-image': `url(${require('@/assets/curso/tema1/img17.png')})`}"
      )
      .bloque-texto-g__texto.p-4
        p.mb-0 Cuando compra #[i software] y lo instala en su computadora, es usted el propietario de ese #[i software]? Tcnicamente, no! Cuando se instala #[i software], en realidad solo se le otorga una licencia para usarlo. Cuando se instala por primera vez un paquete de #[i software], se le pide que acepte los trminos de servicio o el acuerdo de licencia. En ese acuerdo, encontrar que sus derechos de uso del #[i software] estn limitados. Por ejemplo, en los trminos de la licencia del #[i software] Microsoft Office Excel 2010, encontrar la siguiente declaracin: Este #[i software] tiene licencia, no se vende. Este acuerdo solo le otorga algunos derechos para usar las funciones incluidas en la edicin de #[i software] que obtuvo con licencia.
    .row.bg-c11.mb-5
      .col-lg-6.bg-c10.shadow-sm.p-5.my-4(data-aos="fade-right")
        p.mb-0 En su mayor parte, estas restricciones son lo que esperara: no puede hacer copias ilegales del #[i software] y no puede usarlo para hacer nada ilegal. Sin embargo, existen otros trminos ms inesperados en estos acuerdos de #[i software]. Por ejemplo, muchos acuerdos de #[i software] le piden que acepte un lmite de responsabilidad. Nuevamente, de Microsoft: Limitacin y exclusin de daos. Puede recuperar de Microsoft y sus proveedores solo los daos directos hasta el monto que pag por el #[i software]. No puede recuperar ningn otro dao, incluidos daos consecuentes, prdida de ganancias, daos especiales, indirectos o incidentales. Lo que esto significa es que, si un problema con el #[i software] causa dao a su empresa, no puede responsabilizar a Microsoft ni al proveedor por los daos.
    .row.mb-4(data-aos="fade-right")
      .col-auto.px-4.py-3.bg-c3
        h3.mb-0 Aplicaciones para la Empresa
    .row.align-items-center.mb-5
      .col-lg-3.mb-4.mb-lg-0(data-aos="fade-right")
        figure
          img(src='@/assets/curso/tema1/img27.svg', style="width: 350px", alt='Imgen decorativa').m-auto
      .col-lg-9(data-aos="fade-left")
        .cajon.color-primario.p-4
          p.mb-0 A medida que la computadora personal proliferaba dentro de las organizaciones, el control sobre la informacin generada por la organizacin comenz a fragmentarse. Digamos que el departamento de servicio al cliente crea una base de datos de clientes para realizar un seguimiento de las llamadas y los informes de problemas, y el departamento de ventas tambin crea una base de datos para realizar un seguimiento de la informacin del cliente. Cul debe utilizarse como lista maestra de clientes? Como otro ejemplo, alguien en ventas podra crear una hoja de clculo para calcular los ingresos por ventas, mientras que alguien en finanzas crea una diferente que satisfaga las necesidades de su departamento. Sin embargo, es probable que las dos hojas de clculo presenten diferentes totales de ingresos. Cul es el correcto? Y quin gestiona toda esta informacin?
    .row.mb-4(data-aos="fade-right")
      .col-auto.px-4.py-3.bg-c3
        h3.mb-0 Planificacin de recursos empresariales
    .row.align-items-center.mb-5
      .col-lg-7.mb-4.mb-lg-0(data-aos="fade-right")
        p.mb-0 En la dcada de 1990, se hizo ms evidente la necesidad de volver a poner la informacin de la organizacin bajo un control centralizado. El sistema de planificacin de recursos empresariales (ERP) (a veces llamado simplemente #[i software] empresarial) se desarroll para reunir a toda una organizacin en una aplicacin de #[i software]. En pocas palabras, un sistema ERP es una aplicacin de #[i software] que utiliza una base de datos central que se implementa en toda la organizacin. Echemos un vistazo ms de cerca a esta definicin:
      .col-lg-5(data-aos="fade-left")
        figure
          img(src='@/assets/curso/tema1/img19.png', style="width: 550px", alt='Imgen decorativa').m-auto
    .row.bg-c12
      .col-12
        .p-5
          .row.justify-content-center
            .col-lg-10(data-aos="fade-down")
              PasosA.color-acento-contenido.mb-5(tipo="n")
                .row.align-items-center
                  .col.mb-4.mb-lg-0
                    p Un ERP es una aplicacin que utilizan muchos de los empleados de una organizacin. Utilizando una base de datos central: Todos los usuarios del ERP editan y guardan su informacin de la fuente de datos. Lo que esto significa en la prctica es que solo hay una base de datos de clientes, solo hay un clculo de ingresos, etc.
                  .col-lg-auto
                    figure
                      img(src='@/assets/curso/tema1/img28.svg', style="width: 170px", alt='Imgen decorativa').m-auto
                .row.align-items-center
                  .col-lg-auto.mb-4.mb-lg-0
                    figure
                      img(src='@/assets/curso/tema1/img29.svg', style="width: 170px", alt='Imgen decorativa').m-auto
                  .col
                    p.txt-c2 Los sistemas ERP incluyen una funcionalidad que cubre todos los componentes esenciales de un negocio. Adems, una organizacin puede adquirir mdulos para su sistema ERP que satisfagan necesidades especficas, como la fabricacin o la planificacin.
                .row.align-items-center
                  .col.mb-4.mb-lg-0
                    p Los sistemas ERP se comercializaron originalmente para grandes corporaciones. Sin embargo, a medida que ms y ms empresas grandes comenzaron a instalarlos, los proveedores de ERP comenzaron a apuntar a empresas medianas e incluso ms pequeas. Algunos de los sistemas ERP ms conocidos incluyen los de SAP, Oracle y Microsoft.
                  .col-lg-auto
                    figure
                      img(src='@/assets/curso/tema1/img30.svg', style="width: 170px", alt='Imgen decorativa').m-auto
                .row.align-items-center
                  .col-lg-auto.mb-4.mb-lg-0
                    figure
                      img(src='@/assets/curso/tema1/img31.svg', style="width: 170px", alt='Imgen decorativa').m-auto
                  .col
                    p.txt-c2 Para implementar efectivamente un sistema ERP, la organizacin debe estar lista para comprometerse por completo, pues todos los aspectos de la organizacin se ven afectados ya que los sistemas antiguos son reemplazados por el sistema ERP. En general, implementar un sistema ERP puede llevar de dos a tres aos y varios millones de dlares. En la mayora de los casos, el costo del #[i software] no es la parte ms cara de la implementacin: es el costo de los consultores!
                .row.align-items-center
                  .col.mb-4.mb-lg-0
                    p Un sistema ERP puede brindarle a una organizacin un buen retorno de su inversin. Al consolidar los sistemas de informacin en toda la empresa y usar el #[i software] para hacer cumplir las mejores prcticas, la mayora de las organizaciones ven una mejora general despus de implementar un ERP.
                  .col-lg-auto
                    figure
                      img(src='@/assets/curso/tema1/img32.svg', style="width: 170px", alt='Imgen decorativa').m-auto
    .row.justify-content-center.mb-5
      .col-auto.bg-c13.fig-p3(data-aos="fade-right")
        .p-3
          p.fs-4.text-center.mb-0 #[b Gestin de la relacin con el cliente ]
      .col-lg-10.bg-c14(data-aos="fade-left")
        .row.justify-content-center
          .col-auto
            figure.fig-p4
              img(src='@/assets/curso/tema1/img34.svg', style="width: 550px", alt='Imgen decorativa').m-auto
      .col-lg-8.bg-c15(data-aos="fade-down")
        .p-4
          p.mb-0.mt-4 Un sistema de gestin de relaciones con los clientes (CRM) es una aplicacin de #[i software] diseada para gestionar los clientes de una organizacin. En el entorno actual, es importante desarrollar relaciones con sus clientes, y el uso de un CRM bien diseado puede permitir que una empresa personalice su relacin con cada uno de sus clientes. Algunos sistemas de #[i software]  ERP incluyen mdulos CRM. Un ejemplo de un paquete de CRM muy conocido es <i>Salesforce</i>.

    .row.mb-4(data-aos="fade-right")
      .col-auto.px-4.py-3.bg-c3
        h3.mb-0 Gestin de la cadena de suministro

    p.mb-5 Muchas organizaciones deben afrontar la compleja tarea de gestionar sus cadenas de suministro, aclaremos un poco dicho trmino:

    .row.justify-content-center.mb-5(data-aos="fade-left")
      .col-lg-10
        .row.justify-content-center.align-items-center
          .col-xl-4 
            figure
              img(src='@/assets/curso/tema1/img34-2.svg', style="width: 380px", alt='Imgen decorativa').m-auto
          .col-xl-auto
            figure.fig-p2
              img(src='@/assets/curso/tema1/img6.svg', style="width: 50px", alt='Imgen decorativa').m-auto
          .col-md-8.col-xl-7.bg-c5.p-5
            p.text-white.mb-0 Es el proceso que da entre diferentes eslabones de en un proceso de produccin, proveedores instalaciones de fabricacin, distribuidores de los productos, los cuales trabajan en pro de garantizar la satisfaccin del cliente.


    .cajon.color-primario.p-4.mb-5(data-aos="fade-right")
      p.mb-0 Un sistema de gestin de la cadena de suministro (SCM) gestiona la interconexin entre estos eslabones, as como el inventario de los productos en sus distintas etapas de desarrollo.
    .row.mb-4(data-aos="fade-right")
      .col-auto.px-4.py-3.bg-c3
        h3.mb-0 Aplicaciones mviles
    .row.align-items-center.mb-5
      .col-lg-3.mb-4.mb-lg-0(data-aos="fade-right")
        figure
          img(src='@/assets/curso/tema1/img35.svg', style="width: 170px", alt='Imgen decorativa').m-auto
      .col-lg-9(data-aos="fade-left")
        .cajon.color-acento-contenido.p-4
          p.mb-0 Al igual que la computadora personal, los dispositivos mviles como tabletas y telfonos inteligentes tambin tienen sistemas operativos y #[i software] de aplicacin. De hecho, estos dispositivos mviles son, en muchos sentidos, solo versiones ms pequeas de computadoras personales. Una aplicacin mvil es una aplicacin de #[i software] programada para ejecutarse especficamente en un dispositivo mvil.
    .bloque-texto-g.bloque-texto-g--inverso.color-primario.p-3.p-sm-4.p-md-5.mb-5(data-aos="fade-left")
      .bloque-texto-g__img(
        :style="{'background-image': `url(${require('@/assets/curso/tema1/img20.png')})`}"
      )
      .bloque-texto-g__texto.p-4
        p.mb-0 Saba que, actualmente, la mayora de los dispositivos mviles funcionan con uno de dos sistemas operativos, Android o iOS. Android es un sistema operativo de cdigo abierto comprado y respaldado por Google; iOS es el sistema operativo mvil de Apple. En el cuarto trimestre de 2012, Android se instal en el 70,1 % de todos los telfonos mviles enviados, seguido del 21,0 % para iOS. Otros sistemas operativos mviles a destacar son BlackBerry (3,2%) y Windows (2,6%).
    .row.mb-4(data-aos="fade-right")
      .col-auto.px-4.py-3.bg-c3
        h3.mb-0 Computacin en la nube
    p(data-aos="fade-right") Histricamente, para que un #[i software] funcionara se tena que instalar una copia individual de este en la computadora, sin embargo, el concepto de computacin en la nube cambia todo, la nube hace referencia a aplicaciones, servicios y almacenamiento de datos que estn conectados a travs de protocolos de Internet. La computacin en la nube es el uso de estos servicios por parte de individuos y organizaciones.
    .row.align-items-start.mb-5
      .col-lg-4.mb-4.mb-lg-0(data-aos="fade-right")
        figure
          img(src='@/assets/curso/tema1/img36.svg', style="width: 250px", alt='Imgen decorativa').m-auto
      .col-lg-8(data-aos="fade-left")
        p.mb-0 Si se accede al correo electrnico a travs de un navegador web, se est utilizando una forma de computacin en la nube. Si se utilizan las aplicaciones de Google Drive, se est utilizando la computacin en la nube. Si bien estas son versiones gratuitas de computacin en la nube, existe un gran negocio al proporcionar aplicaciones y almacenamiento de datos a travs de la web.
    p.text-center.mb-5(data-aos="fade-right") #[b A continuacin, se describen algunas ventajas y desventajas de la computacin en la nube:]

    figure.mb-5(data-aos="fade-left")
      img(src='@/assets/curso/tema1/img37.svg', style="width: 1150px", alt='Ventajas a. No hay software para instalar o actualizaciones para mantener. b. Disponible desde cualquier computadora que tenga acceso a Internet. c. Puede escalar a una gran cantidad de usuarios fcilmente. d. Las nuevas aplicaciones pueden estar listas y funcionando muy rpidamente. e. Los servicios se pueden arrendar por un tiempo limitado segn sea necesario. f. Su informacin no se pierde si su disco duro falla o si le roban su computadora porttil. g. No est limitado por la memoria disponible o el espacio en disco de su computadora. Desventajas a. Su informacin se almacena en la computadora de otra persona, qu tan segura es? b. Se debe tener acceso a Internet para usarlo. Si no se tienes acceso, es imposible acceder a la informacin. c. Est confiando en un tercero para proporcionar estos servicios, en muchos casos se requiere de un proveedor para solucionar los problemas que pueden presentarse.').m-auto
    
    .row.align-items-center.mb-5
      .col-lg-4.mb-4.mb-lg-0(data-aos="fade-right")
        figure
          img(src='@/assets/curso/tema1/img21.png', style="width: 400px", alt='Imgen decorativa').m-auto
      .col-lg-8(data-aos="fade-left")
        .row.mb-4
          .col-auto.px-4.py-3.bg-c3
            h3.mb-0 Uso de una nube privada
        p.mb-5 Es comprensible que muchas organizaciones estn nerviosas por ceder el control de sus datos y algunas de sus aplicaciones mediante el uso de la computacin en la nube. Una solucin a este problema radica en el concepto de nube privada en la cual proveedor de servicios en la nube brinda un espacio del servidor web para que determinada organizacin tenga gran control sobre ese espacio del servidor.
        .row.mb-4
          .col-auto.px-4.py-3.bg-c3
            h3.mb-0 Virtualizacin
        p.mb-0 La virtualizacin es el proceso de usar #[i software] para simular una computadora o algn otro dispositivo. Por ejemplo, usando la virtualizacin, una sola computadora puede realizar las funciones de varias computadoras. Empresas como EMC proporcionan #[i software] de virtualizacin que permite a los proveedores de servicios en la nube proporcionar servidores web a sus clientes de manera rpida y eficiente. Las organizaciones tambin estn implementando la virtualizacin para reducir la cantidad de servidores necesarios para brindar sus servicios.
    .row.mb-4(data-aos="fade-right")
      .col-auto.px-4.py-3.bg-c3
        h3.mb-0 Creacin de #[i software]
    p(data-aos="fade-down") Cmo se crea el #[i software]? Si el #[i software] es el conjunto de instrucciones que le dice al #[i hardware] qu hacer, cmo se escriben estas instrucciones? Si una computadora lee todo como unos y ceros, tenemos que aprender a escribir #[i software] de esa manera?
    .row.align-items-center.mb-0.mb-lg-5
      .col-lg-8.mb-4.mb-lg-0(data-aos="fade-right")
        .cajon.color-primario.p-4.mb-3
          p.mb-0 Las aplicaciones de #[i software] modernas se escriben utilizando un lenguaje de programacin el cual consta de un conjunto de comandos que se pueden organizar lgicamente para ejecutar funciones especficas.
        p.mb-0 Este lenguaje generalmente consiste en un conjunto de palabras legibles combinadas con smbolos. Con este lenguaje, un programador escribe un programa (llamado cdigo fuente) que luego se puede compilar en un formato legible por mquina, los unos y los ceros necesarios para que la CPU los ejecute. Los ejemplos de lenguajes de programacin conocidos en la actualidad incluyen Java, PHP y varias versiones de C (Visual C, C++, C#). Se utilizan lenguajes como HTML y JavaScript para desarrollar pginas web. La mayora de las veces, la programacin se realiza dentro de un entorno de programacin; cuando compra una copia de Visual Studio de Microsoft, le proporciona un editor, compilador.
      .col-lg-4.d-none.d-lg-block(data-aos="fade-left")
        figure
          img(src='@/assets/curso/tema1/img22.png', style="width: 400px", alt='Imgen decorativa').m-auto
    .row.justify-content-center.mb-5(data-aos="fade-left")
      .col-lg-10
        .row.justify-content-center.align-items-center
          .col-xl-4 
            figure
              img(src='@/assets/curso/tema1/img38.svg', style="width: 380px", alt='Imgen decorativa').m-auto
          .col-xl-auto
            figure.fig-p2
              img(src='@/assets/curso/tema1/img6.svg', style="width: 50px", alt='Imgen decorativa').m-auto
          .col-md-8.col-xl-7.bg-c5.p-4
            p.text-white.mb-0 La programacin de #[i software] era originalmente un proceso individual, con cada programador trabajando en un programa completo, o varios programadores trabajando cada uno en una parte de un programa ms grande. Sin embargo, los mtodos ms nuevos de desarrollo de #[i software] incluyen un enfoque ms colaborativo, con equipos de programadores que trabajan juntos en el cdigo.
    .row.mb-4(data-aos="fade-right")
      .col-auto.px-4.py-3.bg-c3
        h3.mb-0 #[i Software] de cdigo abierto
    .bloque-texto-g.color-primario.p-3.p-sm-4.p-md-5.mb-5(data-aos="fade-left")
      .bloque-texto-g__img(
        :style="{'background-image': `url(${require('@/assets/curso/tema1/img23.png')})`}"
      )
      .bloque-texto-g__texto.p-4
        p.mb-0 Cuando se lanz por primera vez la computadora personal, no cubra ninguna necesidad prctica. Las primeras computadoras eran difciles de programar y requeran una gran atencin a los detalles. Sin embargo, muchos entusiastas de las computadoras personales se unieron de inmediato para crear y compartir aplicaciones para resolver problemas.
    .row.align-items-center.mb-5
      .col-lg-8.mb-4.mb-lg-0(data-aos="fade-right")
        .cajon.color-primario.p-4.mb-3
          p.mb-0 Sin embargo, a medida que el #[i software] comenz a convertirse en un negocio, esta idea de compartir todo cay en desgracia, al menos entre algunos. Cuando un programa de #[i software] requiere cientos de horas de trabajo para desarrollarse, es comprensible que los programadores no quieran simplemente regalarlo. Esto condujo a un nuevo modelo comercial de licencias de #[i software] restrictivas, que requera el pago por el #[i software], un modelo que todava es dominante en la actualidad. Este modelo a veces se denomina fuente cerrada, ya que el cdigo fuente no est disponible para otros.
        p.mb-0 Sin embargo, hay muchos que sienten que el #[i software] no debera estar restringido. Al igual que aquellos primeros aficionados en la dcada de 1970, sienten que la innovacin y el progreso pueden lograrse mucho ms rpidamente si compartimos lo que aprendemos. En la dcada de 1990, con el acceso a Internet conectando a ms y ms personas, el movimiento de cdigo abierto cobr fuerza.
      .col-lg-4(data-aos="fade-left")
        figure
