    Separador
    #t_1_2
    .titulo-segundo.color-acento-botones
      h2 1.2 Funcionalidades de los dispositivos informticos
    .row.bg-c1
      .col-12
        .px-5
          figure(data-aos="fade-right")
            img(src='@/assets/curso/tema1/img28.png', style="width: 1200px", alt='Imgen decorativa').m-auto
          .row.justify-content-center.mb-5(data-aos="fade-down")
            .col-lg-10.bg-c2.shadow-sm.p-4
              p.mb-0 Casi todas las empresas utilizan computadoras para llevar a cabo sus funciones. Desde la generacin de informes hasta la comunicacin con los clientes, las computadoras hacen mucho por la eficiencia con la que se ejecuta un negocio.
          .bloque-texto-g.color-primario.p-3.p-sm-4.p-md-5.mb-5(data-aos="fade-down")
            .bloque-texto-g__img(
              :style="{'background-image': `url(${require('@/assets/curso/tema1/img29.png')})`}"
            )
            .bloque-texto-g__texto.p-4
              p.mb-0 Existen varios tipos de computadoras para computadoras porttiles, PC, servidores e incluso telfonos inteligentes. Gracias a las computadoras, conceptos como los horarios de trabajo flexibles y las fuerzas de trabajo remotas se han vuelto posibles, lo que permite a los empleados trabajar desde donde quieran y en el momento que deseen.
                br
                br
                | Revise a continuacin las diversas funcionalidades que presentan las computadoras:
    .row.mb-5
      .col-12.bg-c18
        .px-4.py-5
          .row
            .col-12.bg-c19
              .p-4
                SlyderE(indicadores="derecha")
                  .row
                    .col-lg-8.mb-4.mb-lg-0
                      p.txt-c1.fs-5 #[b Para la comunicacin]
                      p Cuando se trata de establecer contacto con los clientes, las computadoras son una herramienta vital; tambin son fundamentales a la hora de mantener ese contacto. Este proceso de comunicacin se da a travs de correo electrnico, mensajera instantnea, Skype, #[i software] de colaboracin, entre otras. 
                      p.mb-0 Adems, una empresa necesita comunicarse con sus empleados, y las computadoras juegan un papel importante. En lugar de tener reuniones individuales, se puede enviar un correo electrnico o un mensaje en cualquier otra plataforma de comunicacin aceptable. Esto ahorra tiempo y tambin mejora la comunicacin interna de la empresa.
                    .col-lg-4
                      figure
                        img(src='@/assets/curso/tema1/img40.svg', style="width: 450px", alt='Imgen decorativa').m-auto
                  .row
                    .col-lg-8.mb-4.mb-lg-0
                      p.txt-c1.fs-5 #[b Para la comercializacin]
                      p Las computadoras permiten que una empresa pueda posicionarse, el equipo de desarrolladores de TI de una empresa puede crear un sitio web profesional completo con grficos atractivos y contenido como texto, imgenes y videos. Pueden hacer optimizacin de motor de bsqueda (SEO) que le permita ser reconocida por el pblico objetivo.
                      p.mb-0 Con una computadora, una empresa puede crear, ejecutar o adquirir campaas de #[i marketing] completas que abarcan todas las plataformas de redes sociales en Internet, La computadora es probablemente el mejor dispositivo de #[i marketing] jams construido!
                    .col-lg-4
                      figure
                        img(src='@/assets/curso/tema1/img41.svg', style="width: 450px", alt='Imgen decorativa').m-auto
                  .row
                    .col-lg-8.mb-4.mb-lg-0
                      p.txt-c1.fs-5 #[b Para la contabilidad]
                      p La contabilidad es una funcin en la que la precisin es de suma importancia. Cuando realiza funciones contables, y no tiene nada ms que lpiz y papel, y confa nicamente en el poder del cerebro, se expone a errores. El #[i software] de contabilidad ayuda a evitar que eso suceda, pues este permite que una empresa obtenga de forma rpida y precisa imgenes a gran y pequea escala de la situacin financiera de la empresa.
                      p.mb-0  Las computadoras tambin son fundamentales para tareas como facturar a los clientes; mantenimiento de datos sobre deudores y acreedores; clculo de nmina, clculo y presentacin de formularios de impuestos, as como para muchas otras funciones.
                    .col-lg-4
                      figure
                        img(src='@/assets/curso/tema1/img42.svg', style="width: 450px", alt='Imgen decorativa').m-auto
                  .row
                    .col-lg-8.mb-4.mb-lg-0
                      p.txt-c1.fs-5 #[b Para el almacenamiento]
                      p.mb-0 Las empresas pueden almacenar y clasificar millones de archivos con computadoras y servidores, lo que les permite acceder a ellos en cualquier momento. Pueden optar por almacenar los datos de diferentes maneras: centralizados, permitiendo a otras computadoras en la red acceder a ellos con permisos adecuados, o localmente en computadoras individuales. El almacenamiento digital ofrece mayor capacidad y eficiencia que el almacenamiento fsico, gracias a la clasificacin de archivos. Adems, proporciona mayor seguridad al ser ms difcil perder archivos en bases de datos bien protegidas.
                    .col-lg-4
                      figure
                        img(src='@/assets/curso/tema1/img43.svg', style="width: 450px", alt='Imgen decorativa').m-auto
                  .row
                    .col-lg-8.mb-4.mb-lg-0
                      p.txt-c1.fs-5 #[b Para producir documentos]
                      p La mayora de las empresas necesitan manejar gran cantidad de documentos. Las computadoras proporcionan procesadores de texto y #[i software] de hoja de clculo para ayudar con esto. Con aplicaciones de hojas de clculo, una empresa tiene el poder de manipular datos alfanumricos y organizarlos en tablas, grficos e informes. Otro tipo de #[i software] que utilizan las empresas es el #[i software] de presentacin con el que pueden apoyar sus exposiciones. 
                      p.mb-0 En general, una computadora tiene muchos #[i software]s utilizados para facilitar las funciones de la empresa, hacindola ms rpida y eficiente de lo que era hace 20 o 30 aos.
                    .col-lg-4
                      figure
                        img(src='@/assets/curso/tema1/img44.svg', style="width: 450px", alt='Imgen decorativa').m-auto
                  .row
                    .col-lg-8.mb-4.mb-lg-0
                      p.txt-c1.fs-5 #[b Con fines educativos]
                      p.mb-0 Una empresa puede usar una computadora para educar a los empleados sobre cosas como la poltica de la empresa, el uso del #[i software], as como los procedimientos estndar y la seguridad. En lugar de contratar maestros para ensear a sus empleados, una empresa puede permitir que los empleados aprendan lo que necesitan, aprendiendo a su propio ritmo a travs de seminarios web y sesiones de preguntas y respuestas en vivo. Con Internet, se tiene la posibilidad de acceder a un mundo de informacin a travs de diferentes medios, como videos, texto e incluso juegos, lo que les permite comprender mejor el tema.
                    .col-lg-4
                      figure
                        img(src='@/assets/curso/tema1/img45.svg', style="width: 450px", alt='Imgen decorativa').m-auto
                  .row
                    .col-lg-8.mb-4.mb-lg-0
                      p.txt-c1.fs-5 #[b Para la investigacin empresarial]
                      p.mb-0 Las computadoras permiten que una empresa haga una variedad de cosas, incluida la investigacin sobre la competencia, as como sobre lo que quieren los clientes. Con tcnicas modernas de minera de datos, una empresa puede adquirir informacin importante para la toma de decisiones, incluidos foros, motores de bsqueda, sitios web especficos de la industria e incluso de las redes sociales.
                    .col-lg-4
                      figure
                        img(src='@/assets/curso/tema1/img46.svg', style="width: 450px", alt='Imgen decorativa').m-auto
                  .row
                    .col-lg-8.mb-4.mb-lg-0
                      p.txt-c1.fs-5 #[b Para hacer negocios]
                      p.mb-0 La tecnologa cambia la definicin de la frase "hacer negocios". Con las computadoras, puede producir ms documentos de los que una mquina de escribir y un mimegrafo podran producir. Las computadoras simplifican las transacciones comerciales y permiten que los productos y servicios se vendan en todo el mundo. En una oficina totalmente equipada con una red informtica, tiene la capacidad de comunicarse a travs de la intranet de la empresa, as como a travs de Internet. Una intranet es extremadamente valiosa; puede manejar informacin delicada o confidencial dentro de los lmites de la red de su empresa sin preocuparse de que usuarios no autorizados accedan a la red privada de su organizacin. Las computadoras tambin son necesarias para las organizaciones con empleados que trabajan a distancia o estn ubicados de forma remota.
                    .col-lg-4
                      figure
                        img(src='@/assets/curso/tema1/img47.svg', style="width: 450px", alt='Imgen decorativa').m-auto
                  .row
                    .col-lg-8.mb-4.mb-lg-0
                      p.txt-c1.fs-5 #[b Para Recursos Humanos]
                      p Los sistemas de informacin de recursos humanos, llamados HRIS, son extremadamente populares, permiten un procesamiento eficiente de solicitudes. En su serie de libros blancos E-HR: La revolucin tecnolgica en recursos humanos: definicin y beneficios, <i>Right Team</i> Inc. seala: Un sistema de recursos humanos de ltima generacin proporciona los datos, las herramientas y la tecnologa subyacentes para administrar y ejecutar actividades de recursos humanos en un entorno integrado.
                      p.mb-0 Los sistemas HRIS recopilan datos para informes, directos, oficiales, federales, estatales y locales, como formularios de igualdad de oportunidades de empleo que algunas empresas deben enviar a las agencias gubernamentales. Los sistemas HRIS tambin clasifican los datos de empleo, procesan nmina, calculan beneficios de los empleados, realizan auditoras de recursos humanos, analizan tasas de rotacin y recopilan las respuestas de los empleados a las encuestas de opinin y las entrevistas de salida.
                    .col-lg-4
                      figure
                        img(src='@/assets/curso/tema1/img48.svg', style="width: 450px", alt='Imgen decorativa').m-auto
                  .row
                    .col-lg-8.mb-4.mb-lg-0
                      p.txt-c1.fs-5 #[b Para expandir el alcance comercial]
                      p La globalizacin es una forma moderna de describir hasta qu punto una empresa puede llegar a sus clientes o cmo una organizacin puede producir materiales o artculos para usuarios finales de cualquier parte del mundo. Las computadoras le permiten comercializar los productos y servicios de su empresa en un rea geogrfica local o en clientes de todo el mundo. Como dice Ghana Web, "La introduccin de las computadoras e Internet ha reducido el mundo a una aldea global donde los individuos, las empresas y los pases pueden intercambiar bienes y servicios".
                      p.mb-0 Sin el beneficio de las computadoras en el lugar de trabajo, seleccionar datos demogrficos para identificar y acercarse a su mercado consumira una enorme cantidad de tiempo y energa del personal. Las computadoras proporcionan funciones rentables para la produccin, las ventas y el envo.
                    .col-lg-4
                      figure
                        img(src='@/assets/curso/tema1/img49.svg', style="width: 450px", alt='Imgen decorativa').m-auto
    .row.mb-4(data-aos="fade-right")
      .col-auto.px-4.py-3.bg-c3
        h3.mb-0 Funciones TI en una organizacin
    .row.align-items-center.mb-5
      .col-lg-7.mb-4.mb-lg-0(data-aos="fade-right")
        p.mb-0 Con las computadoras y las redes informticas, las personas han podido lograr en el lapso de unas pocas dcadas lo que de otro modo nos hubiera llevado muchos siglos lograr. La gente ha democratizado Internet, ha mejorado enormemente la comunicacin, ha aumentado la capacidad de disear e inventar por mltiples y ha hecho la vida ms fcil en general. Ahora las personas llevan pequeos dispositivos informticos en sus bolsillos que brindan asistencia en casi todos los aspectos de la vida, ya sea para despertarlos por la maana, recordarles eventos importantes o ayudarnos a pensar y aprender nueva informacin.
      .col-lg-5(data-aos="fade-left")
        figure
          img(src='@/assets/curso/tema1/img30.png', style="width: 450px", alt='Imgen decorativa').m-auto
    .row.justify-content-center.mb-5(data-aos="fade-left")
      .col-lg-10
        .row.justify-content-center.align-items-center
          .col-xl-4 
            figure
