          .col-md-8.col-xl-7.bg-c5.p-4
            p.text-white.mb-0 Se puede decir que los pequeos dispositivos informticos que la gente lleva consigo se han convertido de alguna manera en parte de nosotros. Se han convertido en rganos adicionales en nuestros cuerpos, como brazos o piernas. Tal vez podamos pensar en ellos como una especie de mejora en nuestro cerebro, permitindonos lograr cosas que antes nunca imaginamos posibles. De la misma manera, TI se ha convertido en un rgano adicional en el cuerpo de cualquier negocio, lo que le permite lograr cosas mucho ms grandes de lo que podra sin la ayuda de TI.
    p.mb-5(data-aos="fade-down") El departamento de TI es un departamento importante dentro de una organizacin, que complementa a los dems de muchas maneras, en el siguiente documento, se presenta sus funciones ms importantes:
    .row.justify-content-center.mb-5
      .col-lg-8(data-aos="fade-left")
