    .row.mb-4(data-aos="fade-right")
      .col-auto.px-4.py-3.bg-c3
        h3.mb-0 Tecnologa de la informacin y sus usos en la gestin empresarial
    .row.align-items-center.mb-5
      .col-lg-8.mb-4.mb-lg-0(data-aos="fade-right")
        .cajon.color-primario.p-4.mb-3
          p.mb-0 Con ms innovacin en tecnologa, se crean nuevos negocios, con ms negocios, la tecnologa viene al rescate facilitando las cosas. Los dos existen en una especie de relacin simbitica que asegura que siempre coexistirn. El negocio ha existido desde tiempos prehistricos. Puede haber comenzado con nada ms que el comercio de trueque, si hay que creer en los libros de historia, pero desde entonces se ha transformado en algo mucho ms complejo, y nada de eso habra sido posible sin la tecnologa.
        p Las principales industrias del mundo colapsaran si la existencia y el uso de la tecnologa de la informacin fueran arrebatados repentinamente a las empresas. Esto se debe a que la mayora de las transacciones y operaciones comerciales no se pueden realizar en el siglo XXI sin tecnologa. 
        p.mb-0 Revise puntualmente la funcin de la tecnologa en las organizaciones:
      .col-lg-4(data-aos="fade-left")
        figure
          img(src='@/assets/curso/tema1/img31.png', style="width: 450px", alt='Imgen decorativa').m-auto
    .row.bg-c12
      .col-12(data-aos="fade-down")
        .p-5
          ImagenInfografica.color-secundario
            template(v-slot:imagen)
              figure
                img(src='@/assets/curso/tema1/img52.svg', alt='Funciones de la tecnologa en las organizaciones, verificar su descripcin abajo de la imagen.')
            .tarjeta.color-acento-contenido.p-3(x="13%" y="45%" numero="+")
              .row.align-items-center
                .col-lg.mb-4.mb-lg-0
                  p.mb-0 Con los aos, debido a la tecnologa, se revolucionaron muchos modelos comerciales tradicionales. La tecnologa abri la oportunidad de ver las cosas desde una nueva perspectiva pues ha permitido una mayor eficiencia a la hora de  hacer negocios y el uso de las TIC ha influenciado los procesos de gestin, los sistemas de contabilidad y otros aspectos complejos de las actividades comerciales cotidianas.
                .col-lg-auto
                  figure
                    img(src='@/assets/curso/tema1/img53.svg', style="width: 80px", alt='Imgen decorativa').m-auto
            .tarjeta.color-acento-contenido.p-3(x="90%" y="30%" numero="+")
              .row.align-items-center
                .col-lg.mb-4.mb-lg-0
                  p.mb-0 La tecnologa permite automatizar numerosos procesos, aumentando la productividad. La tecnologa facilita el almacenamiento y garantiza la   integridad y confidencialidad de la informacin, gracias a ella una empresa puede almacenar sus datos de manera segura y tenerla a la mano en cualquier momento para tomar decisiones asertivas que la beneficien y la posicionan frente a la competencia.  
                .col-lg-auto
                  figure
                    img(src='@/assets/curso/tema1/img54.svg', style="width: 80px", alt='Imgen decorativa').m-auto
            .tarjeta.color-acento-contenido.p-3(x="60%" y="40%" numero="+")
              .row.align-items-center
                .col-lg.mb-4.mb-lg-0
                  p.mb-0 Con la tecnologa, ha sido posible globalizar las operaciones comerciales, ahora, cualquier persona puede hacer negocios desde cualquier lugar, esto ha hecho posible que las empresas tengan un alcance ms amplio en el mundo, el Internet es ahora una parte crucial de la campaa de #[i marketing] de cualquier empresa, ya que le permite atraer clientes en todo el mundo. Sin embargo, tambin existen amenazas para los negocios provocadas por la tecnologa, estos incluyen actividades maliciosas como por ejemplo la piratera.
                .col-lg-auto
                  figure
                    img(src='@/assets/curso/tema1/img55.svg', style="width: 80px", alt='Imgen decorativa').m-auto
    .bloque-texto-g.color-acento-contenido.p-3.p-sm-4.p-md-5.mb-5(data-aos="fade-right")
      .bloque-texto-g__img(
        :style="{'background-image': `url(${require('@/assets/curso/tema1/img32.png')})`}"
      )
      .bloque-texto-g__texto.p-4
        p.mb-0 La Revolucin Industrial cambi las cosas en el mundo de los negocios, haciendo que muchos procesos fueran ms eficientes y aumentando la productividad cien veces. Sin embargo, el mundo de los negocios permaneci algo estancado durante un siglo despus. Sin embargo, con la revolucin tecnolgica y el uso de la tecnologa en los negocios, las cosas cambiaron de manera an ms disruptiva que durante la Revolucin Industrial y sera seguro decir que las cosas nunca volvern a ser las mismas. La velocidad a la que la tecnologa est evolucionando y adaptndose es exponencial hasta el punto en que todas las empresas estn siendo barridas por la ola, ya sea que estn listas para ello o no. Puede parecer que no hemos progresado tanto, pero incluso hace solo 5 aos, las redes sociales no tenan ningn consumismo, los telfonos mviles no se usaban para los negocios, las soluciones basadas en la nube no existan.
    .row.align-items-center.mb-5
      .col-auto(data-aos="fade-right")
        figure
          img(src='@/assets/curso/tema1/img1.svg', style="width: 30px", alt='Imgen decorativa').m-auto
      .col(data-aos="fade-left")
        p.mb-0 La tecnologa ha cambiado todos los aspectos de los negocios de manera acelerada y esto nunca haba sucedido en la historia. Para ser ms especficos, aqu hay algunas formas en que la tecnologa de la informacin ha afectado a los negocios:
    .row.align-items-center.mb-5
      .col-lg-8.mb-4.mb-lg-0(data-aos="fade-right")
        AcordionA(tipo="a" clase-tarjeta="tarjeta tarjeta--azul1")
          .row(titulo="El advenimiento de las soluciones mviles")
            .col-lg-5.mb-4.mb-lg-0
              figure
                img(src='@/assets/curso/tema1/img57.svg', style="width: 230px", alt='Imgen decorativa').m-auto
            .col-lg-7
              p.mb-0 La movilidad es fundamental para las empresas, ahora los negocios pueden manejarse utilizando una tableta o un telfono inteligente, desde all pueden hacer #[i marketing] de contenido, establecer relaciones con los clientes, realizar ventas, manejar los aspectos administrativos, etc., por su parte los consumidores usan sus telfonos para comprar, vender y compartir experiencias.
          .row(titulo="El fenmeno de la computacin en la nube")
            .col-lg-5.mb-4.mb-lg-0
              figure
                img(src='@/assets/curso/tema1/img58.svg', style="width: 230px", alt='Imgen decorativa').m-auto
            .col-lg-7
              p.mb-0 Ha hecho posible que las empresas subcontraten muchas de sus funciones a terceros a travs de Internet, esto ha permitido que las empresas se expandan rpidamente y adopten la movilidad sin tener que preocuparse por cosas como bloqueos, tiempo de inactividad y prdida de datos, la computacin en la nube ha permitido el manejo de informacin de forma eficiente y segura.
          .row(titulo="Mayor segmentacin de clientes")
            .col-lg-5.mb-4.mb-lg-0
              figure
                img(src='@/assets/curso/tema1/img59.svg', style="width: 230px", alt='Imgen decorativa').m-auto
            .col-lg-7
              p.mb-0 Gracias al anlisis de datos se puede obtener una visin ms amplia de las necesidades de los clientes. Algo tan simple como tener una cuenta de Google permite a una empresa saber de dnde es un usuario, el tipo de navegador que est usando, qu hace en ese sitio web, sus requerimientos, etc.
          .row(titulo="Mayor conectividad")
            .col-lg-5.mb-4.mb-lg-0
              figure
                img(src='@/assets/curso/tema1/img60.svg', style="width: 230px", alt='Imgen decorativa').m-auto
            .col-lg-7
              p.mb-0 La tecnologa ha facilitado que las personas se mantengan en contacto. Ya sea que est buscando comunicarse con sus empleados y colegas a travs de videochat o enviando correos electrnicos masivos a clientes potenciales, la tecnologa mvil y la innovacin constante que tiene lugar dentro del espacio han hecho posible que la comunicacin adquiera un nuevo nivel de hiperrealismo.
          .row(titulo="Costos decrecientes y utilidad creciente")
            .col-lg-5.mb-4.mb-lg-0
              figure
                img(src='@/assets/curso/tema1/img61.svg', style="width: 230px", alt='Imgen decorativa').m-auto
            .col-lg-7
              p.mb-0 El "mercado de compradores" se ha hecho posible gracias a la asequibilidad del #[i hardware] y #[i software], y al aumento de empresarios expertos en tecnologa. Las soluciones ofrecidas ms fcilmente han facilitado el desarrollo del negocio sin grandes inversiones.
          .row(titulo="Mayor consideracin por el impacto social de los negocios")
            .col-lg-5.mb-4.mb-lg-0
              figure
                img(src='@/assets/curso/tema1/img62.svg', style="width: 230px", alt='Imgen decorativa').m-auto
            .col-lg-7
              p.mb-0 Las redes sociales han hecho del mundo un lugar ms pequeo donde los usuarios pueden conectarse sin importar quines son. Por ende, la empresa ahora debe hacer un esfuerzo adicional si no quiere calificaciones desfavorables en los sitios web. Por lo tanto, se debe tener cuidado con la reputacin en lnea y se debe trabajar en la huella digital.
          .row(titulo="El fin del tiempo de inactividad")
            .col-lg-5.mb-4.mb-lg-0
              figure
                img(src='@/assets/curso/tema1/img63.svg', style="width: 230px", alt='Imgen decorativa').m-auto
            .col-lg-7
              p.mb-0 Esto es en realidad un efecto negativo de la tecnologa. Con una mayor conectividad, las personas tienen cada vez menos tiempo para s mismas. Las vacaciones parecen haberse convertido en algo del pasado, Siempre se puede acceder a los correos electrnicos, mensajes de texto y redes sociales a travs de los telfonos y computadoras, cada vez es ms difcil desconectarse.
      .col-lg-4(data-aos="fade-left")
        figure
          img(src='@/assets/curso/tema1/img56.svg', style="width: 430px", alt='Imgen decorativa').m-auto
    p.mb-5(data-aos="fade-right") Puede ser un poco difcil comprender la importancia de la tecnologa de la informacin para una organizacin si no es un profesional de TI. Sin embargo, existen numerosas formas en las que la tecnologa de la informacin es crucial para una organizacin, revselas a continuacin:
    .row.bg-c12.mb-5
      .col-12(data-aos="fade-right")
        .p-5
          ImagenInfografica.color-secundario
            template(v-slot:imagen)
              figure
                img(src='@/assets/curso/tema1/img64.svg', alt='Imgen decorativa')
            .tarjeta.color-acento-botones.p-3(x="60%" y="20%" numero="+")
              .row
                .col-12
                  p.mb-0 Es importante que la educacin pueda mantenerse al da con el progreso que ocurre en la tecnologa, preparando a los estudiantes a prepararse para el futuro. Los estudiantes de nuestras aulas de hoy estn destinados a implementar la innovacin y la creatividad dentro de su diario vivir, por ende deben conocer y manejar todo tipo de dispositivos que le permitan construir conocimiento como   computadoras, telfonos mviles y tabletas, deben reconocer uso de Internet como medio de aprendizaje.
            .tarjeta.color-acento-botones.p-3(x="80%" y="42%" numero="+")
              .row
                .col-12
                  p.mb-0 Con las mejoras en la tecnologa de la informacin, cada vez es ms fcil reformar el sector de la salud. Los consultorios mdicos ahora pueden compartir informacin entre ellos y pueden obtener datos de salud de mdicos anteriores. Esto hace posible que se brinde una atencin oportuna, as como que se reduzcan los costos.
            .tarjeta.color-acento-botones.p-3(x="79%" y="73%" numero="+")
              .row
                .col-12
                  p.mb-0 Con un nmero cada vez mayor de transacciones que se realizan en lnea, existe una necesidad cada vez mayor de seguridad. La tecnologa de la informacin es lo que hace posible mantener los datos e informacin seguros y solo accesibles para el usuario. Mediante el uso de encriptacin y contraseas, los datos digitales se ocultan de forma segura y solo aquellos que tienen permiso pueden acceder a ellos.
            .tarjeta.color-acento-botones.p-3(x="19%" y="73%" numero="+")
              .row
                .col-12
                  p.mb-0 El mundo de los negocios cambi para siempre cuando las computadoras entraron en escena. Las empresas pueden utilizar la tecnologa de la informacin mediante el uso de computadoras y diferentes programas para ejecutar sus operaciones de una manera ms fluida. Lo usan en diferentes departamentos, incluidos finanzas, fabricacin, recursos humanos y seguridad.
            .tarjeta.color-acento-botones.p-3(x="19.3%" y="40%" numero="+")
              .row
                .col-12
                  p.mb-0 Con un nmero cada vez mayor de transacciones en lnea, es importante que las instituciones financieras y de seguridad trabajen juntas para hacer de Internet un lugar seguro. A medida que se realicen ms transacciones en Internet, se necesitarn ms redes y mayor seguridad, lo que har posible que los bancos mantengan seguras las compras y las ventas.
    p.mb-5(data-aos="fade-down") Como propietario de un negocio, probablemente se est preguntando cmo puede utilizar la tecnologa en su mximo potencial para su negocio, a continuacin, algunos consejos:
    .row.bg-c20.mb-5
      .col-12(data-aos="fade-left")
        .p-5
          figure
            img(src='@/assets/curso/tema1/img66.svg', alt='1)	Comunicacin: use la tecnologa para integrar la comunicacin dentro de su organizacin. 2)	Uso de plataformas: deje que sus empleados estn en sintona entre s, utilizando cosas como plataformas de colaboracin y redes sociales para mantener a todos informados sobre lo que sucede en el negocio y hacia dnde se dirige. De esa manera, estarn sincronizados con los objetivos del negocio y su productividad mejorar significativamente. 3)	Servicio al cliente: utilice la tecnologa para mejorar el servicio que brinda a sus clientes, permitindoles brindarle comentarios sobre sus productos y servicios, as como sugerencias sobre lo que puede mejorar. 4)	Seguridad: con la mejora de los estndares de seguridad, tambin puede eliminar las contraseas por completo y probar los sistemas de seguridad biomtricos, que no requieren que recuerde una docena de contraseas a la vez.').m-auto

    .row.mb-4(data-aos="fade-right")
      .col-auto.px-4.py-3.bg-c3
        h3.mb-0 Formas efectivas de comunicacin organizacional

    .bloque-texto-g.color-secundario.p-3.p-sm-4.p-md-5.mb-5(data-aos="fade-right")
      .bloque-texto-g__img(
        :style="{'background-image': `url(${require('@/assets/curso/tema1/img69.png')})`}"
      )
      .bloque-texto-g__texto.p-4
        p.mb-0 Las diferentes situaciones comerciales y las diferentes necesidades de los empleados exigen el uso de mltiples canales de comunicacin para llevar los mensajes a casa de manera efectiva. Una comprensin de los beneficios de varios tipos de comunicacin organizacional puede ayudar a los empresarios a comunicarse de manera ms efectiva, revselos a continuacin:

    .tarjeta.tarjeta--azul2.mb-5(data-aos="fade-right")
      .p-4
        SlyderA(tipo="b")
          .row.align-items-center.justify-content-center
            .col-lg-6.mb-4.mb-lg-0
              p #[b Cara a cara]
              p.mb-0 La tecnologa nunca reemplazar el beneficio y el poder de la comunicacin cara a cara. Cuando tenga dudas, o cuando una comunicacin sea crtica y sea posible reunirse de manera presencial, hgalo. La conexin personal que se produce cuando dos o ms personas pueden interactuar en tiempo real es significativa.
            .col-lg-5
              figure
                img(src='@/assets/curso/tema1/img33.png', style="width: 430px", alt='Imgen decorativa').m-auto
          .row.align-items-center.justify-content-center
            .col-lg-6.mb-4.mb-lg-0
              p #[b Correo electrnico]
              p.mb-0 Este probablemente ha cambiado el entorno de comunicacin organizacional de manera ms significativa que cualquier otra introduccin de tecnologa en las ltimas dcadas, segn <i>Thinkquest</i>. El correo electrnico es rpido, eficiente y permite un registro de la interaccin. Pero, si bien el correo electrnico puede ser muy efectivo, los empresarios deben evitar usarlo en exceso. A veces, el dilogo directo con una persona es lo mejor.
            .col-lg-5
              figure
                img(src='@/assets/curso/tema1/img34.png', style="width: 430px", alt='Imgen decorativa').m-auto
          .row.align-items-center.justify-content-center
            .col-lg-6.mb-4.mb-lg-0
              p #[b Telfono]
              p.mb-0 Sigue siendo un pilar en cualquier entorno empresarial, aunque cada vez ms de estos telfonos se estn convirtiendo en la variedad mvil. A veces, el correo electrnico funciona bien; a veces nada supera la conexin ms personal que se puede lograr por telfono, incluso con colegas a cientos o miles de millas de distancia.
            .col-lg-5
              figure
                img(src='@/assets/curso/tema1/img35.png', style="width: 430px", alt='Imgen decorativa').m-auto
          .row.align-items-center.justify-content-center
            .col-lg-6.mb-4.mb-lg-0
              p #[b Reuniones del Ayuntamiento]
              p.mb-0 La oportunidad de reunir a un grupo de empleados en una sala para una reunin puede ser una forma muy efectiva de comunicacin organizacional, segn Lin Grensing-Pophal, autora de <i>Employee Management for Small Business</i>. Ofrece el poder de las conexiones interpersonales y brinda una oportunidad para que los empleados se escuchen y aprendan unos de otros.
            .col-lg-5
              figure
                img(src='@/assets/curso/tema1/img36.png', style="width: 430px", alt='Imgen decorativa').m-auto
          .row.align-items-center.justify-content-center
            .col-lg-6.mb-4.mb-lg-0
              p #[b Video conferencia]
              p.mb-0 Puede ser una excelente manera de comunicarse con los empleados, especialmente cuando estn separados por la distancia. El Internet ha facilitado las comunicaciones comerciales y la grabacin y el acercamiento a travs de video. Se espera que el iPad tenga un impacto an mayor en la facilidad de uso y la prevalencia del video en el entorno empresarial, segn Steve Vonder Haar, director de investigacin y fundador de <i>Interactive Media Strategies</i>.
            .col-lg-5
              figure
                img(src='@/assets/curso/tema1/img37.png', style="width: 430px", alt='Imgen decorativa').m-auto
    p.mb-5(data-aos="fade-right") La tecnologa ha alterado la vida moderna de muchas maneras, especialmente en el lugar de trabajo. La invencin de las computadoras, la miniaturizacin de la electrnica y el desarrollo de la comunicacin inalmbrica han alterado el mundo de los negocios. La comunicacin empresarial, en particular, ha experimentado algunos de los mayores avances debido a:
    .row.align-items-center.mb-5
      .col-lg-4.mb-4.mb-lg-0(data-aos="fade-right")
        figure
          img(src='@/assets/curso/tema1/img68.svg', style="width: 430px", alt='Imgen decorativa').m-auto
      .col-lg-8(data-aos="fade-left")
        AcordionA(tipo="a" clase-tarjeta="tarjeta tarjeta--azul1")
          .row(titulo="Desarrollo telfono celular")
            .col-12
              p.mb-0 Uno de los mayores avances en la comunicacin ha sido el desarrollo del telfono celular. En el pasado, su nica oportunidad de comunicarse con los empleados era cuando estaban en sus escritorios, lo que provocaba interminables juegos de "etiquetas telefnicas" cuando las personas intentaban conectarse durante los das de trabajo ocupados. Ahora, se puede contactar a los trabajadores sin importar dnde se encuentren, e incluso fuera del horario laboral. El desarrollo de los telfonos inteligentes tambin ha ampliado en gran medida los tipos de actividades comerciales que puede realizar cuando est fuera de la oficina, aumentando la productividad y extendiendo la jornada laboral.
          .row(titulo="Dispositivos de comunicacin")
            .col-12
              p.mb-0 Por supuesto, los telfonos no son los nicos dispositivos de comunicacin de alta tecnologa. Las computadoras vienen en una amplia variedad de configuraciones en estos das, incluidas tabletas, computadoras porttiles pequeas pero potentes, dispositivos 2 en 1 que sirven como computadora porttil o tableta, y otras variaciones. Todos estos dispositivos tienen capacidades inalmbricas incorporadas y tambin se pueden configurar con comunicaciones mviles, lo que los convierte, en esencia, en telfonos celulares grandes. Cualquiera que sea el dispositivo que lleve una persona de negocios, facilita las comunicaciones verbales, de texto y de imgenes.
          .row(titulo="Teleconferencia")
            .col-12
              p.mb-0 Las conexiones de datos de alta velocidad permiten el uso de teleconferencias, reuniones virtuales realizadas a travs de enlaces de audio y video. Las teleconferencias pueden ahorrar cantidades sustanciales de dinero que de otro modo se gastaran en viajes al conectar a empleados importantes en sucursales remotas para compartir ideas e informacin. El uso de pizarras virtuales, plataformas comunitarias de intercambio de datos donde los usuarios remotos pueden interactuar como si estuvieran alrededor de la misma mesa, aumenta an ms las posibilidades del lugar de trabajo virtual.
          .row(titulo="Escaneo de imgenes")
            .col-12
              p.mb-0 Permiten a los trabajadores convertir documentos, planos, diagramas y fotografas en archivos electrnicos rpidamente para su almacenamiento y transmisin. En lugar de depender de la entrega en mano de documentos importantes en toda la ciudad o en todo el pas, los empleados pueden escanear y enviar estos archivos en cuestin de segundos a travs de Internet. Un estudio de arquitectura podra, por ejemplo, escanear actualizaciones de un plano, transmitirlo al cliente, recibir cambios y realizar modificaciones, todo antes de que un mensajero hubiera podido cruzar la ciudad por primera vez.
          .row(titulo="Etiquetas por radiofrecuencia")
            .col-12
              p.mb-0 El desarrollo de la identificacin por radiofrecuencia (RFID) ha cambiado sustancialmente el campo de la logstica comercial y, junto con otros avances en comunicacin comercial, tiene el potencial de aumentar significativamente la eficiencia de una empresa. Las etiquetas RFID son pequeos chips que responden a las ondas de radio con informacin codificada, lo que permite a las empresas etiquetar y rastrear artculos y materiales. Cuando un empleado activa un dispositivo de escaneo, todas las etiquetas RFID dentro del alcance respondern con su informacin codificada, lo que permite actualizaciones rpidas de los inventarios del almacn y seguimiento en tiempo real de los envos a medida que pasan por la cadena de suministro. Poder acceder al inventario ha pedido permite a las empresas aprovechar el envo "justo a tiempo".
    .row.mb-4(data-aos="fade-right")
      .col-auto.px-4.py-3.bg-c3
        h3.mb-0 Cmo usar las herramientas informticas de manera adecuada?
    p.mb-5(data-aos="fade-right") Usar correctamente, las herramientas informticas pueden ayudar a la empresa a ser ms organizada y mucho ms eficiente, a continuacin, algunas estrategias:
    .row.justify-content-center
      .col-lg-8(data-aos="fade-left")
        .tarjeta.color-primario.p-3
          .row.justify-content-around.align-items-center
            .col-3.col-sm-2.col-lg-1
              img(src="@/assets/curso/tema1/img51.svg")
            .col
              .row.justify-content-between.align-items-center
                .col.mb-3.mb-sm-0
                  p.text-white.mb-0 #[b Anexo]. Estrategias para implementar de manera adecuada las herramientas informticas.
                .col-sm-auto
